/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;500;600;700&display=swap");
a,
button {
  text-decoration: none;
  background: none;
  text-align: center;
}

* {
  margin: 0;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(9%, #228183), color-stop(92%, #f5ddb7));
  background: linear-gradient(180deg, #228183 9%, #f5ddb7 92%);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  color: #fff;
  font-weight: lighter;
  line-height: 1.5;
}

.App {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-height: 100vh;
}

@media (max-height: 600px) {
  .App {
    height: auto;
  }
}

@media only screen and (max-width: 37.5em) {
  .App {
    min-height: 100vh;
  }
}

img {
  width: 100%;
}

p {
  margin: 1.5rem 0;
  text-align: left;
}

.Main {
  width: 50%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
}

@media only screen and (max-width: 48em) {
  .Main {
    width: 100%;
    padding: 2.5rem;
  }
}

.Main__text {
  margin-left: 2rem;
  margin-right: 2rem;
}

@media only screen and (max-width: 48em) {
  .Main__text {
    margin-left: 0rem;
    margin-right: 0rem;
  }
}

@media only screen and (max-width: 37.5em) {
  .Main__text {
    margin-left: 0rem;
    margin-right: 0rem;
  }
}

.Main__logo {
  width: 18rem;
  margin-bottom: 2rem;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media only screen and (max-width: 48em) {
  .Main__logo {
    width: 12rem;
    margin-bottom: 2rem;

  }
}

@media only screen and (max-width: 37.5em) {
  .Main__logo {
    width: 12rem;
    margin-bottom: 2rem;

  }
}

.Main__links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

@media only screen and (max-width: 37.5em) {
  .Main__links {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}

.Main__links > * {
  background-color: #fff;
  border-radius: 10px;
  padding: .3rem;
  color: #228183;
  width: 30%;
  margin-bottom: 1rem;
  text-decoration: none;
  border: 1px solid #fff;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media only screen and (max-width: 37.5em) {
  .Main__links > * {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
  }
}

.Main__links > *:hover {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
}

.Main__social_icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-bottom: 2rem;
}

.Main__social_icons > * {
  margin: .7rem;
  text-decoration: none;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-size: 1.4rem;
  color: #fff;
}

.Main__social_icons > *:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  color: #228183;
}
